
import Picker from 'vue-wheel-picker';

export default {
  props: {
    containerclass: {
      type: String,
      default: 'timePicker'
    },
    arrows: {
      type: Boolean,
      default: false
    }
  },
  components: {
    Picker,
  },
  model: {
    prop: 'value',
    event: 'change',
  },
  data() {
    return {
      fromDateMenu: false,
      fromDateVal: null,
      resultHour: null,
      resultMinute: null,
      resultDate: null,
      dateObject: null
    }
  },
  computed: {
    fromDateDisp() {
      const _tmp = new Date(this.resultDate);
      return this.displayDate(_tmp);
    },
    hours() {
      const res = [];
      for (let i = 0; i < 24; i++) {
        res.push({"value": i, "text": i.toString().padStart(2, "0")})
      }
      return res;
    },
    minutes() {
      const res = [];

      for (let i = 0; i < 60; i+=1) {
        res.push({"value": i, "text": i.toString().padStart(2, "0")})
      }
      console.log(res);
      return res;
    }
  },
  mounted() {
    if (this.value) {
      //Expected timestamp JsonTime:2022-07-22T15:46:58.602Z
      this.dateObject = this.$exerciseManager.parseJsonDate(this.value);

    } else {
      this.dateObject = new Date();
    }
    const _h = this.dateObject.getHours();
    const _m = this.dateObject.getMinutes();
    this.resultHour = {"value": parseInt(_h), "text": (parseInt(_h)).toString().padStart(2, "0")}
    this.resultMinute = {"value": parseInt(_m), "text": (parseInt(_m)).toString().padStart(2, "0")}
    this.resultDate = this.formatDate(this.dateObject);
    this.emitNewValue();
  },
  methods: {
    displayDate(dateObject) {
      const day = dateObject.getDate() + "";
      const month = (dateObject.getMonth() + 1) + "";
      const year = dateObject.getFullYear();
      return day.padStart(2, '0') + "/" + month.padStart(2, '0') + "/" + year;
    },
    formatDate(dateObject) {
      const day = dateObject.getDate() + "";
      const month = (dateObject.getMonth() + 1) + "";
      const year = dateObject.getFullYear();
      return year + "-" + month.padStart(2, '0') + "-" + day.padStart(2, '0');
    },
    emitNewValue() {
      const _hours = this.resultHour ? (this.resultHour.value) : 0;
      const _minutes = this.resultMinute ? (this.resultMinute.value) : 0;
      let _dateResult = this.resultDate ? new Date(this.resultDate) : new Date();
      _dateResult.setHours(_hours);
      _dateResult.setMinutes(_minutes);

      const res = _dateResult.toJSON();
      this.$emit('change', res);
    }
  },
  watch: {
    "resultHour": function () {
      this.emitNewValue();
    },
    "resultMinute": function () {
      this.emitNewValue();
    },
    "resultDate": function () {
      this.emitNewValue();
    }
  }
}
