
import Picker from 'vue-wheel-picker';

export default {
  props: {
    containerclass: {
      type: String,
      default: 'timePicker'
    },
    arrows: {
      type: Boolean,
      default: false
    }
  },
  components: {
    Picker,
  },
  model: {
    prop: 'value',
    event: 'change',
  },
  data() {
    return {
      resultHour: null,
      resultMinute: null,
    }
  },
  computed: {
    hours() {
      const res = [];
      for (let i = 0; i < 24; i++) {
        res.push({"value": i, "text": i.toString().padStart(2, "0")})
      }
      return res;
    },
    minutes() {
      const res = [];
      for (let i = 0; i < 60; i += 15) {
        res.push({"value": i, "text": i.toString().padStart(2, "0")})
      }
      return res;
    }
  },
  mounted() {
    if (this.value) {
      let piece = this.value.split(":");
      if (piece.length >= 1) {
        this.resultHour = {"value": parseInt(piece[0]), "text": (parseInt(piece[0])).toString().padStart(2, "0")}
      }
      if (piece.length >= 2) {
        this.resultMinute = {"value": parseInt(piece[1]), "text": (parseInt(piece[1])).toString().padStart(2, "0")}
      }
    } else {
      //const dt = new Date();
      const _h = 0; //dt.getHours();
      const _m = 0; //dt.getMinutes();
      this.resultHour = {"value": parseInt(_h), "text": (parseInt(_h)).toString().padStart(2, "0")}
      this.resultMinute = {"value": parseInt(_m), "text": (parseInt(_m)).toString().padStart(2, "0")}
    }
    this.emitNewValue();
  },
  methods: {
    emitNewValue() {
      let _hours = this.resultHour ? (this.resultHour.value + "") : "00";
      let _minutes = this.resultMinute ? (this.resultMinute.value + "") : "00";
      const res = _hours.padStart(2, "0") + ":" + _minutes.padStart(2, "0");
      this.$emit('change', res);
    }
  },
  watch: {
    "resultHour": function () {
      this.emitNewValue();

    },
    "resultMinute": function () {
      this.emitNewValue();
    }
  }
}
