
import Picker from 'vue-wheel-picker';

export default {
  props: {
    containerclass: {
      type:String,
      default:'numberPicker'
    },
    min: {
      type: Number,
      default: 0
    },

    max: {
      type: Number,
      default: 100
    },
    arrows: {
      type: Boolean,
      default: false
    },
    padstring: {
      type: Boolean,
      default: false
    }
  },
  components: {
    Picker,
  },
  model: {
    prop: 'value',
    event: 'change',
  },
  data() {
    return {
      resultData: null
    }
  },
  computed: {
    radius() {
      const _optionsLn = this.options.length-1;
      if(_optionsLn<=10) {
        return 100;
      }
      const _val = ((Math.round(_optionsLn/10) -1)*20)+100;
      //console.log('count', _optionsLn,_val);
      return Math.max(250,_val);
    },
    options() {
      const res = [];
      for (let i = this.min; i <= this.max; i++) {
        if(this.padstring) {
          res.push({"value": i, "text": i.toString().padStart(2,"0")})
        } else {
          res.push({"value": i, "text": i})
        }
      }
      return res;
    }
  },
  mounted() {
    if(this.value) {
      if(this.padstring) {
        this.resultData = {"value":this.value,"text":this.value.padstring(2,"0")}
      } else {
        this.resultData = {"value":this.value,"text":this.value}
      }
    } else {
      const _opt = this.options;
      this.resultData = _opt[0];
    }
    this.$emit('change', this.resultData.value);
  },
  watch: {
    "resultData": function () {
      this.$emit('change', this.resultData.value);
    }
  }
}
